
.stockHeading
{
  font-weight:bold;
  font-size:1.5em;
  padding-right:0.5em;
}
.zeroLeftPadding
{
  padding-left:0;
}
@media only screen and (max-width: 750px)
{
.zeroLeftPadding
  {
    padding-left:15px;
}
}
/*---- Our css animation for when switching components */
@-webkit-keyframes theSlideIn
{
from
  {
    -webkit-transform:translateX(-20%);
            transform:translateX(-20%);
}
to
  {
    -webkit-transform:translateX(0px);
            transform:translateX(0px);
}
}
@keyframes theSlideIn
{
from
  {
    -webkit-transform:translateX(-20%);
            transform:translateX(-20%);
}
to
  {
    -webkit-transform:translateX(0px);
            transform:translateX(0px);
}
}
@-webkit-keyframes theSlideOut
{
from
  {
    -webkit-transform:translateX(0px);
            transform:translateX(0px);
}
to
  {
    -webkit-transform:translateX(20%);
            transform:translateX(20%);
}
}
@keyframes theSlideOut
{
from
  {
    -webkit-transform:translateX(0px);
            transform:translateX(0px);
}
to
  {
    -webkit-transform:translateX(20%);
            transform:translateX(20%);
}
}
.theSlide-enter
{
  opacity:0;
}
.theSlide-enter-active
{
  -webkit-animation: theSlideIn 0.25s ease-out forwards;
          animation: theSlideIn 0.25s ease-out forwards;
  -webkit-transition: opacity 0.25s;
  transition: opacity 0.25s;
  opacity:1;
}
.theSlide-leave-active
{
  opacity:1;
}
.theSlide-leave-active
{
  -webkit-animation: theSlideOut 0.25s ease-out forwards;
          animation: theSlideOut 0.25s ease-out forwards;
  -webkit-transition: opacity 0.25s;
  transition: opacity 0.25s;
  opacity:0;
}

